import browser from "webextension-polyfill";
import { Prefs } from "../../../adblockpluschrome/lib/prefs";
import { hasSchedule, removeSchedule } from "../../core/scheduled-event-emitter/background";
import { warn as logWarning, reportAndLogError } from "../../logger/background";
import { executeIPMCommands, getPayload, clearEvents } from "@eyeo-fragments/ipm/background";
import { intervalKey, lastPingKey, pingCountPrefsKey, scheduleName, serverUrlKey } from "./telemetry.types";
async function processResponse(response) {
    if (!response || !response.ok) {
        const status = (response === null || response === void 0 ? void 0 : response.status) || "no status";
        reportAndLogError(new Error(`Bad response status from IPM server: ${status}`), `[Telemetry]: Bad response status from IPM server: ${status}`);
        return;
    }
    const body = await response.text();
    if (body.length === 0) {
        return;
    }
    try {
        const bodyJSON = JSON.parse(body);
        let commands;
        if (Array.isArray(bodyJSON)) {
            commands = bodyJSON;
        }
        else {
            commands = [bodyJSON];
        }
        await executeIPMCommands(commands);
    }
    catch (error) {
        reportAndLogError(error, "[Telemetry]: Error parsing IPM response.");
    }
}
function incrementPingCount() {
    const count = Prefs.get(pingCountPrefsKey);
    void Prefs.set(pingCountPrefsKey, count + 1);
}
export async function sendPing() {
    if (Prefs.get("data_collection_opt_out") === true) {
        return;
    }
    const lastPingTimestamp = Prefs.get(lastPingKey);
    const intervalTime = Prefs.get(intervalKey);
    if (Date.now() - lastPingTimestamp < intervalTime) {
        logWarning("[Telemetry]: Ping time too early, not pinging.");
        return;
    }
    const payload = await getPayload();
    void clearEvents();
    const url = Prefs.get(serverUrlKey);
    if (typeof url !== "string") {
        return;
    }
    void Prefs.set(lastPingKey, Date.now());
    incrementPingCount();
    void fetch(url, {
        method: "POST",
        cache: "no-cache",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload)
    })
        .then(processResponse)
        .catch((error) => {
        reportAndLogError(error, "[Telemetry]: Ping sending failed with error:");
    });
}
export async function start() {
    if (hasSchedule(scheduleName)) {
        void removeSchedule(scheduleName);
    }
    await Prefs.untilLoaded;
    const intervalTime = Prefs.get(intervalKey);
    if (typeof intervalTime !== "number") {
        return;
    }
    browser.alarms.onAlarm.addListener((alarm) => {
        if (alarm.name !== scheduleName) {
            return;
        }
        void sendPing();
    });
    browser.alarms.create(scheduleName, {
        periodInMinutes: intervalTime / 1000 / 60
    });
    void sendPing();
}
